import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Pergunta_avaliacao_ideia } from '../../../modelo/Pergunta_avaliacao_ideia'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'pergunta_avaliacao_ideia-selector',   templateUrl: 'pergunta_avaliacao_ideia.component.html' })  export class Pergunta_avaliacao_ideiaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Pergunta_avaliacao_ideia[] = [];     pergunta_avaliacao_ideiaAux : Pergunta_avaliacao_ideia;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.pergunta_avaliacao_ideiaAux = new Pergunta_avaliacao_ideia(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.pergunta_avaliacao_ideia.Pergunta_avaliacao_ideia';         dataAux.objetoJson = this.pergunta_avaliacao_ideiaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Pergunta_avaliacao_ideia. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


