import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Classificacao_ouvidoria } from '../../../modelo/Classificacao_ouvidoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'classificacao_ouvidoria-selector',   templateUrl: 'classificacao_ouvidoria.component.html' })  export class Classificacao_ouvidoriaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Classificacao_ouvidoria[] = [];     classificacao_ouvidoriaAux : Classificacao_ouvidoria;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.classificacao_ouvidoriaAux = new Classificacao_ouvidoria(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.classificacao_ouvidoria.Classificacao_ouvidoria';         dataAux.objetoJson = this.classificacao_ouvidoriaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Classificacao_ouvidoria. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


