import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Config_campo_formulario } from '../../../modelo/Config_campo_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'config_campo_formulario-selector',   templateUrl: 'config_campo_formulario.component.html' })  export class Config_campo_formularioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Config_campo_formulario[] = [];     config_campo_formularioAux : Config_campo_formulario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.config_campo_formularioAux = new Config_campo_formulario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.config_campo_formulario.Config_campo_formulario';         dataAux.objetoJson = this.config_campo_formularioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Config_campo_formulario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


