import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Escalonamento_usuario } from '../../../modelo/Escalonamento_usuario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'escalonamento_usuario-selector',   templateUrl: 'escalonamento_usuario.component.html' })  export class Escalonamento_usuarioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Escalonamento_usuario[] = [];     escalonamento_usuarioAux : Escalonamento_usuario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.escalonamento_usuarioAux = new Escalonamento_usuario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.escalonamento_usuario.Escalonamento_usuario';         dataAux.objetoJson = this.escalonamento_usuarioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Escalonamento_usuario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


