import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Ocorrencia_ouvidoria } from '../../../modelo/Ocorrencia_ouvidoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'ocorrencia_ouvidoria-selector',   templateUrl: 'ocorrencia_ouvidoria.component.html' })  export class Ocorrencia_ouvidoriaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Ocorrencia_ouvidoria[] = [];     ocorrencia_ouvidoriaAux : Ocorrencia_ouvidoria;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.ocorrencia_ouvidoriaAux = new Ocorrencia_ouvidoria(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.ocorrencia_ouvidoria.Ocorrencia_ouvidoria';         dataAux.objetoJson = this.ocorrencia_ouvidoriaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Ocorrencia_ouvidoria. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


