import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Indicador_customizado } from '../../../modelo/Indicador_customizado'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'indicador_customizado-selector',   templateUrl: 'indicador_customizado.component.html' })  export class Indicador_customizadoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Indicador_customizado[] = [];     indicador_customizadoAux : Indicador_customizado;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.indicador_customizadoAux = new Indicador_customizado(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.indicador_customizado.Indicador_customizado';         dataAux.objetoJson = this.indicador_customizadoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Indicador_customizado. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


