import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Container_principal } from '../../../modelo/Container_principal'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'container_principal-selector',   templateUrl: 'container_principal.component.html' })  export class Container_principalComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Container_principal[] = [];     container_principalAux : Container_principal;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.container_principalAux = new Container_principal(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.container_principal.Container_principal';         dataAux.objetoJson = this.container_principalAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Container_principal. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


