import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Periodicidade } from '../../../modelo/Periodicidade'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'periodicidade-selector',   templateUrl: 'periodicidade.component.html' })  export class PeriodicidadeComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Periodicidade[] = [];     periodicidadeAux : Periodicidade;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.periodicidadeAux = new Periodicidade(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.periodicidade.Periodicidade';         dataAux.objetoJson = this.periodicidadeAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Periodicidade. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


