import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Acao_preventiva } from '../../../modelo/Acao_preventiva'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'acao_preventiva-selector',   templateUrl: 'acao_preventiva.component.html' })  export class Acao_preventivaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Acao_preventiva[] = [];     acao_preventivaAux : Acao_preventiva;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.acao_preventivaAux = new Acao_preventiva(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.acao_preventiva.Acao_preventiva';         dataAux.objetoJson = this.acao_preventivaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Acao_preventiva. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


