import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Permissao_padrao } from '../../../modelo/Permissao_padrao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'permissao_padrao-selector',   templateUrl: 'permissao_padrao.component.html' })  export class Permissao_padraoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Permissao_padrao[] = [];     permissao_padraoAux : Permissao_padrao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.permissao_padraoAux = new Permissao_padrao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.permissao_padrao.Permissao_padrao';         dataAux.objetoJson = this.permissao_padraoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Permissao_padrao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


