import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Bug_report } from '../../../modelo/Bug_report'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'bug_report-selector',   templateUrl: 'bug_report.component.html' })  export class Bug_reportComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Bug_report[] = [];     bug_reportAux : Bug_report;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.bug_reportAux = new Bug_report(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.bug_report.Bug_report';         dataAux.objetoJson = this.bug_reportAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Bug_report. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


