import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Plano_acao_ocorrencia } from '../../../modelo/Plano_acao_ocorrencia'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'plano_acao_ocorrencia-selector',   templateUrl: 'plano_acao_ocorrencia.component.html' })  export class Plano_acao_ocorrenciaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Plano_acao_ocorrencia[] = [];     plano_acao_ocorrenciaAux : Plano_acao_ocorrencia;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.plano_acao_ocorrenciaAux = new Plano_acao_ocorrencia(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.plano_acao_ocorrencia.Plano_acao_ocorrencia';         dataAux.objetoJson = this.plano_acao_ocorrenciaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Plano_acao_ocorrencia. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


