import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Reuniao } from '../../../modelo/Reuniao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'reuniao-selector',   templateUrl: 'reuniao.component.html' })  export class ReuniaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Reuniao[] = [];     reuniaoAux : Reuniao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.reuniaoAux = new Reuniao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.reuniao.Reuniao';         dataAux.objetoJson = this.reuniaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Reuniao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


