import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Sistema } from '../../../modelo/Sistema'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'sistema-selector',   templateUrl: 'sistema.component.html' })  export class SistemaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Sistema[] = [];     sistemaAux : Sistema;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.sistemaAux = new Sistema(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.sistema.Sistema';         dataAux.objetoJson = this.sistemaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Sistema. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


