import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Resposta_campo_formulario } from '../../../modelo/Resposta_campo_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'resposta_campo_formulario-selector',   templateUrl: 'resposta_campo_formulario.component.html' })  export class Resposta_campo_formularioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Resposta_campo_formulario[] = [];     resposta_campo_formularioAux : Resposta_campo_formulario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.resposta_campo_formularioAux = new Resposta_campo_formulario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.resposta_campo_formulario.Resposta_campo_formulario';         dataAux.objetoJson = this.resposta_campo_formularioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Resposta_campo_formulario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


