import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Executar_manutencao } from '../../../modelo/Executar_manutencao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'executar_manutencao-selector',   templateUrl: 'executar_manutencao.component.html' })  export class Executar_manutencaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Executar_manutencao[] = [];     executar_manutencaoAux : Executar_manutencao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.executar_manutencaoAux = new Executar_manutencao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.executar_manutencao.Executar_manutencao';         dataAux.objetoJson = this.executar_manutencaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Executar_manutencao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


