import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Documento } from '../../../modelo/Documento'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'documento-selector',   templateUrl: 'documento.component.html' })  export class DocumentoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Documento[] = [];     documentoAux : Documento;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.documentoAux = new Documento(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.documento.Documento';         dataAux.objetoJson = this.documentoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Documento. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


