toggleHeatmap() {
  this.heatmap.setMap(this.heatmap.getMap() ? null : this.map);
}

changeGradient() {
  var gradient = [
    'rgba(0, 255, 255, 0)',
    'rgba(0, 255, 255, 1)',
    'rgba(0, 191, 255, 1)',
    'rgba(0, 127, 255, 1)',
    'rgba(0, 63, 255, 1)',
    'rgba(0, 0, 255, 1)',
    'rgba(0, 0, 223, 1)',
    'rgba(0, 0, 191, 1)',
    'rgba(0, 0, 159, 1)',
    'rgba(0, 0, 127, 1)',
    'rgba(63, 0, 91, 1)',
    'rgba(127, 0, 63, 1)',
    'rgba(191, 0, 31, 1)',
    'rgba(255, 0, 0, 1)'
  ]
  this.heatmap.set('gradient', this.heatmap.get('gradient') ? null : gradient);
}

changeRadius() {
  this.heatmap.set('radius', this.heatmap.get('radius') ? 15 : 30);
}

changeOpacity() {
  this.heatmap.set('opacity', this.heatmap.get('opacity') ? null : 0.2);
}

// Heatmap data: 500 Points
getPoints() {
    let locationsAux = [];
    for (let i = 0; i < this.locations.length; i++) {
    	locationsAux[i] = new google.maps.LatLng(this.locations[i][1], this.locations[i][2]);
    }
	
  return locationsAux;
  }





  
