import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Participantes_reuniao_pic } from '../../../modelo/Participantes_reuniao_pic'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'participantes_reuniao_pic-selector',   templateUrl: 'participantes_reuniao_pic.component.html' })  export class Participantes_reuniao_picComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Participantes_reuniao_pic[] = [];     participantes_reuniao_picAux : Participantes_reuniao_pic;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.participantes_reuniao_picAux = new Participantes_reuniao_pic(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.participantes_reuniao_pic.Participantes_reuniao_pic';         dataAux.objetoJson = this.participantes_reuniao_picAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Participantes_reuniao_pic. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


