import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Auditoria } from '../../../modelo/Auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'auditoria-selector',   templateUrl: 'auditoria.component.html' })  export class AuditoriaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Auditoria[] = [];     auditoriaAux : Auditoria;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.auditoriaAux = new Auditoria(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.auditoria.Auditoria';         dataAux.objetoJson = this.auditoriaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Auditoria. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


