import {Injectable} from '@angular/core';
import {Http, Headers, Response} from '@angular/http';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class MapasService {

    private chave_api = 'AIzaSyDNaYmm_ebMMP_yBrzML_KbQAYNU2g_uuM';

	private headers = new Headers({'Access-Control-Allow-Origin': 'http://maps.googleapis.com'});
    
    constructor(private _http:Http) {
        console.log('Mapas Service Init');
    }

    getService() {
        /*return this._http.get('https://maps.googleapis.com/maps/api/js?key=' + this.chave_api + ' async defer').map(
            res=> res.json()
        );*/

        console.log('TENTAR ENVIAR XML');

        var url : string = 'http://maps.googleapis.com/maps/api/js?key=' + this.chave_api;

        /*
        return new Promise((resolve, reject) => {

            var formData: any = new FormData(); 
            var xhr = new XMLHttpRequest();

            console.log(formData);  

            xhr.open("GET", url, true);
            //xhr.setRequestHeader("Authorization", 'Bearer ' + localStorage.getItem('id_token')); 
            xhr.send(formData);
            xhr.onreadystatechange=function() 
            {
                console.log("status " + xhr.status);
                if(xhr.readyState === XMLHttpRequest.DONE && xhr.status == 201){
                    console.log('FUNCIONA');
                    var res = JSON.parse(xhr.responseText);
                    //this.router.navigate(['../PreviousDetailsCmp', {first_name:res.student_id, student_id:res.id}]);
                }
            }
        });
        */

		return this._http.get( url )
                    .map(this.extractData)
                    .catch(this.handleError);
    }

	private extractData(res: Response) {
		if (res.status == 200) {
			return res.json();
		}
		return false;
	}    

    private handleError(error: any) {
        console.error(error);
        return Observable.throw(error.json().error || 'LSD CRUD Server error');
    }
}


