import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Revisao_programa_qualidade } from '../../../modelo/Revisao_programa_qualidade'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'revisao_programa_qualidade-selector',   templateUrl: 'revisao_programa_qualidade.component.html' })  export class Revisao_programa_qualidadeComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Revisao_programa_qualidade[] = [];     revisao_programa_qualidadeAux : Revisao_programa_qualidade;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.revisao_programa_qualidadeAux = new Revisao_programa_qualidade(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.revisao_programa_qualidade.Revisao_programa_qualidade';         dataAux.objetoJson = this.revisao_programa_qualidadeAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Revisao_programa_qualidade. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


