import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Resposta_auditoria } from '../../../modelo/Resposta_auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'resposta_auditoria-selector',   templateUrl: 'resposta_auditoria.component.html' })  export class Resposta_auditoriaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Resposta_auditoria[] = [];     resposta_auditoriaAux : Resposta_auditoria;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.resposta_auditoriaAux = new Resposta_auditoria(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.resposta_auditoria.Resposta_auditoria';         dataAux.objetoJson = this.resposta_auditoriaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Resposta_auditoria. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


