import { Component, OnInit } from '@angular/core';
//import { Observable } from 'rxjs/Observable';
import { Router } from '@angular/router';
import { ControleService } from '../../core/services/controle/controle.service';

declare var google: any;

@Component({ 
  moduleId: module.id,
  selector: 'rota_destino-selector', 
  templateUrl: 'rota_destino.component.html',
  styleUrls: ['./rota_destino.component.css']
})

export class Rota_destinoComponent implements OnInit {
  
  constructor(private controleApp: ControleService) {
    console.log('Mapas Rota Init');
  }

  	ngOnInit() {

      var infoWindow : any;
      var map = new google.maps.Map(document.getElementById('map'), {
        center: {lat: 0, lng: 0},
        zoom: 16
      });

      var pos = {
        lat: this.controleApp.latitudeUsuario,
        lng: this.controleApp.longitudeUsuario
      };

      var directionsService = new google.maps.DirectionsService;
      var directionsDisplay = new google.maps.DirectionsRenderer;
      directionsDisplay.setMap(map);

      directionsService.route({
        origin: pos,
        destination: "-25.092018,-50.164554",
        travelMode: google.maps.TravelMode.DRIVING
      }, function(response, status) {
        if (status === google.maps.DirectionsStatus.OK) {
          console.log(response);
          directionsDisplay.setDirections(response);
        } else {
          window.alert('Directions request failed due to ' + status);
        }
      });

      map.setCenter(pos);
  	}
}


