import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Avaliacao_reuniao } from '../../../modelo/Avaliacao_reuniao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'avaliacao_reuniao-selector',   templateUrl: 'avaliacao_reuniao.component.html' })  export class Avaliacao_reuniaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Avaliacao_reuniao[] = [];     avaliacao_reuniaoAux : Avaliacao_reuniao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.avaliacao_reuniaoAux = new Avaliacao_reuniao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.avaliacao_reuniao.Avaliacao_reuniao';         dataAux.objetoJson = this.avaliacao_reuniaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Avaliacao_reuniao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


