import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Pais } from '../../../modelo/Pais'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'pais-selector',   templateUrl: 'pais.component.html' })  export class PaisComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Pais[] = [];     paisAux : Pais;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.paisAux = new Pais(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.pais.Pais';         dataAux.objetoJson = this.paisAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Pais. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


