import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Ocorrencia } from '../../../modelo/Ocorrencia'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'ocorrencia-selector',   templateUrl: 'ocorrencia.component.html' })  export class OcorrenciaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Ocorrencia[] = [];     ocorrenciaAux : Ocorrencia;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.ocorrenciaAux = new Ocorrencia(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.ocorrencia.Ocorrencia';         dataAux.objetoJson = this.ocorrenciaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Ocorrencia. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


