import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Executar_reuniao } from '../../../modelo/Executar_reuniao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'executar_reuniao-selector',   templateUrl: 'executar_reuniao.component.html' })  export class Executar_reuniaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Executar_reuniao[] = [];     executar_reuniaoAux : Executar_reuniao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.executar_reuniaoAux = new Executar_reuniao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.executar_reuniao.Executar_reuniao';         dataAux.objetoJson = this.executar_reuniaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Executar_reuniao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


