import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Notificacao } from '../../../modelo/Notificacao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'notificacao-selector',   templateUrl: 'notificacao.component.html' })  export class NotificacaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Notificacao[] = [];     notificacaoAux : Notificacao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.notificacaoAux = new Notificacao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.notificacao.Notificacao';         dataAux.objetoJson = this.notificacaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Notificacao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


