import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Municipio } from '../../../modelo/Municipio'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'municipio-selector',   templateUrl: 'municipio.component.html' })  export class MunicipioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Municipio[] = [];     municipioAux : Municipio;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.municipioAux = new Municipio(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.municipio.Municipio';         dataAux.objetoJson = this.municipioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Municipio. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


