import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Evento_campanha } from '../../../modelo/Evento_campanha'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'evento_campanha-selector',   templateUrl: 'evento_campanha.component.html' })  export class Evento_campanhaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Evento_campanha[] = [];     evento_campanhaAux : Evento_campanha;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.evento_campanhaAux = new Evento_campanha(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.evento_campanha.Evento_campanha';         dataAux.objetoJson = this.evento_campanhaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Evento_campanha. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


