import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Pergunta_auditoria } from '../../../modelo/Pergunta_auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'pergunta_auditoria-selector',   templateUrl: 'pergunta_auditoria.component.html' })  export class Pergunta_auditoriaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Pergunta_auditoria[] = [];     pergunta_auditoriaAux : Pergunta_auditoria;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.pergunta_auditoriaAux = new Pergunta_auditoria(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.pergunta_auditoria.Pergunta_auditoria';         dataAux.objetoJson = this.pergunta_auditoriaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Pergunta_auditoria. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


