import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Avaliacao_ideia } from '../../../modelo/Avaliacao_ideia'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'avaliacao_ideia-selector',   templateUrl: 'avaliacao_ideia.component.html' })  export class Avaliacao_ideiaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Avaliacao_ideia[] = [];     avaliacao_ideiaAux : Avaliacao_ideia;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.avaliacao_ideiaAux = new Avaliacao_ideia(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.avaliacao_ideia.Avaliacao_ideia';         dataAux.objetoJson = this.avaliacao_ideiaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Avaliacao_ideia. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


