import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Unidade } from '../../../modelo/Unidade'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'unidade-selector',   templateUrl: 'unidade.component.html' })  export class UnidadeComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Unidade[] = [];     unidadeAux : Unidade;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.unidadeAux = new Unidade(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.unidade.Unidade';         dataAux.objetoJson = this.unidadeAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Unidade. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


