import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Natureza_ocorrencia } from '../../../modelo/Natureza_ocorrencia'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'natureza_ocorrencia-selector',   templateUrl: 'natureza_ocorrencia.component.html' })  export class Natureza_ocorrenciaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Natureza_ocorrencia[] = [];     natureza_ocorrenciaAux : Natureza_ocorrencia;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.natureza_ocorrenciaAux = new Natureza_ocorrencia(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.natureza_ocorrencia.Natureza_ocorrencia';         dataAux.objetoJson = this.natureza_ocorrenciaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Natureza_ocorrencia. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


