import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Participantes_treinamento } from '../../../modelo/Participantes_treinamento'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'participantes_treinamento-selector',   templateUrl: 'participantes_treinamento.component.html' })  export class Participantes_treinamentoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Participantes_treinamento[] = [];     participantes_treinamentoAux : Participantes_treinamento;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.participantes_treinamentoAux = new Participantes_treinamento(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.participantes_treinamento.Participantes_treinamento';         dataAux.objetoJson = this.participantes_treinamentoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Participantes_treinamento. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


