import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Endereco } from '../../../modelo/Endereco'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'endereco-selector',   templateUrl: 'endereco.component.html' })  export class EnderecoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Endereco[] = [];     enderecoAux : Endereco;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.enderecoAux = new Endereco(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.endereco.Endereco';         dataAux.objetoJson = this.enderecoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Endereco. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


