import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Proposta_candidato } from '../../../modelo/Proposta_candidato'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'proposta_candidato-selector',   templateUrl: 'proposta_candidato.component.html' })  export class Proposta_candidatoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Proposta_candidato[] = [];     proposta_candidatoAux : Proposta_candidato;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.proposta_candidatoAux = new Proposta_candidato(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.proposta_candidato.Proposta_candidato';         dataAux.objetoJson = this.proposta_candidatoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Proposta_candidato. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


