import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Produto_compra } from '../../../modelo/Produto_compra'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'produto_compra-selector',   templateUrl: 'produto_compra.component.html' })  export class Produto_compraComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Produto_compra[] = [];     produto_compraAux : Produto_compra;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.produto_compraAux = new Produto_compra(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.produto_compra.Produto_compra';         dataAux.objetoJson = this.produto_compraAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Produto_compra. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


