import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Notificacao_automatica } from '../../../modelo/Notificacao_automatica'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'notificacao_automatica-selector',   templateUrl: 'notificacao_automatica.component.html' })  export class Notificacao_automaticaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Notificacao_automatica[] = [];     notificacao_automaticaAux : Notificacao_automatica;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.notificacao_automaticaAux = new Notificacao_automatica(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.notificacao_automatica.Notificacao_automatica';         dataAux.objetoJson = this.notificacao_automaticaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Notificacao_automatica. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


