import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Config_envio_indicadores } from '../../../modelo/Config_envio_indicadores'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'config_envio_indicadores-selector',   templateUrl: 'config_envio_indicadores.component.html' })  export class Config_envio_indicadoresComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Config_envio_indicadores[] = [];     config_envio_indicadoresAux : Config_envio_indicadores;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.config_envio_indicadoresAux = new Config_envio_indicadores(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.config_envio_indicadores.Config_envio_indicadores';         dataAux.objetoJson = this.config_envio_indicadoresAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Config_envio_indicadores. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


