import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Classificacao_helpdesk } from '../../../modelo/Classificacao_helpdesk'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'classificacao_helpdesk-selector',   templateUrl: 'classificacao_helpdesk.component.html' })  export class Classificacao_helpdeskComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Classificacao_helpdesk[] = [];     classificacao_helpdeskAux : Classificacao_helpdesk;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.classificacao_helpdeskAux = new Classificacao_helpdesk(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.classificacao_helpdesk.Classificacao_helpdesk';         dataAux.objetoJson = this.classificacao_helpdeskAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Classificacao_helpdesk. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


