import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Atividade_plano_acao } from '../../../modelo/Atividade_plano_acao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'atividade_plano_acao-selector',   templateUrl: 'atividade_plano_acao.component.html' })  export class Atividade_plano_acaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Atividade_plano_acao[] = [];     atividade_plano_acaoAux : Atividade_plano_acao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.atividade_plano_acaoAux = new Atividade_plano_acao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.atividade_plano_acao.Atividade_plano_acao';         dataAux.objetoJson = this.atividade_plano_acaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Atividade_plano_acao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


