import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Execucao_formulario } from '../../../modelo/Execucao_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'execucao_formulario-selector',   templateUrl: 'execucao_formulario.component.html' })  export class Execucao_formularioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Execucao_formulario[] = [];     execucao_formularioAux : Execucao_formulario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.execucao_formularioAux = new Execucao_formulario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.execucao_formulario.Execucao_formulario';         dataAux.objetoJson = this.execucao_formularioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Execucao_formulario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


