import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Veiculo } from '../../../modelo/Veiculo'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'veiculo-selector',   templateUrl: 'veiculo.component.html' })  export class VeiculoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Veiculo[] = [];     veiculoAux : Veiculo;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.veiculoAux = new Veiculo(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.veiculo.Veiculo';         dataAux.objetoJson = this.veiculoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Veiculo. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


