import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Grupo_auditoria } from '../../../modelo/Grupo_auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'grupo_auditoria-selector',   templateUrl: 'grupo_auditoria.component.html' })  export class Grupo_auditoriaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Grupo_auditoria[] = [];     grupo_auditoriaAux : Grupo_auditoria;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.grupo_auditoriaAux = new Grupo_auditoria(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.grupo_auditoria.Grupo_auditoria';         dataAux.objetoJson = this.grupo_auditoriaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Grupo_auditoria. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


