import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Sub_formulario } from '../../../modelo/Sub_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'sub_formulario-selector',   templateUrl: 'sub_formulario.component.html' })  export class Sub_formularioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Sub_formulario[] = [];     sub_formularioAux : Sub_formulario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.sub_formularioAux = new Sub_formulario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.sub_formulario.Sub_formulario';         dataAux.objetoJson = this.sub_formularioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Sub_formulario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


