import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Grupo_usuario_programa } from '../../../modelo/Grupo_usuario_programa'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'grupo_usuario_programa-selector',   templateUrl: 'grupo_usuario_programa.component.html' })  export class Grupo_usuario_programaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Grupo_usuario_programa[] = [];     grupo_usuario_programaAux : Grupo_usuario_programa;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.grupo_usuario_programaAux = new Grupo_usuario_programa(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.grupo_usuario_programa.Grupo_usuario_programa';         dataAux.objetoJson = this.grupo_usuario_programaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Grupo_usuario_programa. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


