import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Pessoa_juridica } from '../../../modelo/Pessoa_juridica'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'pessoa_juridica-selector',   templateUrl: 'pessoa_juridica.component.html' })  export class Pessoa_juridicaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Pessoa_juridica[] = [];     pessoa_juridicaAux : Pessoa_juridica;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.pessoa_juridicaAux = new Pessoa_juridica(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.pessoa_juridica.Pessoa_juridica';         dataAux.objetoJson = this.pessoa_juridicaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Pessoa_juridica. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


