import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Plano_acao_nc } from '../../../modelo/Plano_acao_nc'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'plano_acao_nc-selector',   templateUrl: 'plano_acao_nc.component.html' })  export class Plano_acao_ncComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Plano_acao_nc[] = [];     plano_acao_ncAux : Plano_acao_nc;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.plano_acao_ncAux = new Plano_acao_nc(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.plano_acao_nc.Plano_acao_nc';         dataAux.objetoJson = this.plano_acao_ncAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Plano_acao_nc. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


