import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Mapa_formulario } from '../../../modelo/Mapa_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'mapa_formulario-selector',   templateUrl: 'mapa_formulario.component.html' })  export class Mapa_formularioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Mapa_formulario[] = [];     mapa_formularioAux : Mapa_formulario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.mapa_formularioAux = new Mapa_formulario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.mapa_formulario.Mapa_formulario';         dataAux.objetoJson = this.mapa_formularioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Mapa_formulario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


