import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Plano_acao_hp } from '../../../modelo/Plano_acao_hp'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'plano_acao_hp-selector',   templateUrl: 'plano_acao_hp.component.html' })  export class Plano_acao_hpComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Plano_acao_hp[] = [];     plano_acao_hpAux : Plano_acao_hp;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.plano_acao_hpAux = new Plano_acao_hp(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.plano_acao_hp.Plano_acao_hp';         dataAux.objetoJson = this.plano_acao_hpAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Plano_acao_hp. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


