import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Profissao } from '../../../modelo/Profissao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'profissao-selector',   templateUrl: 'profissao.component.html' })  export class ProfissaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Profissao[] = [];     profissaoAux : Profissao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.profissaoAux = new Profissao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.profissao.Profissao';         dataAux.objetoJson = this.profissaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Profissao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


