import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Fiscal_sif } from '../../../modelo/Fiscal_sif'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'fiscal_sif-selector',   templateUrl: 'fiscal_sif.component.html' })  export class Fiscal_sifComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Fiscal_sif[] = [];     fiscal_sifAux : Fiscal_sif;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.fiscal_sifAux = new Fiscal_sif(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.fiscal_sif.Fiscal_sif';         dataAux.objetoJson = this.fiscal_sifAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Fiscal_sif. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


