import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Acao_corretiva } from '../../../modelo/Acao_corretiva'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'acao_corretiva-selector',   templateUrl: 'acao_corretiva.component.html' })  export class Acao_corretivaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Acao_corretiva[] = [];     acao_corretivaAux : Acao_corretiva;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.acao_corretivaAux = new Acao_corretiva(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.acao_corretiva.Acao_corretiva';         dataAux.objetoJson = this.acao_corretivaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Acao_corretiva. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


