import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Log_documento } from '../../../modelo/Log_documento'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'log_documento-selector',   templateUrl: 'log_documento.component.html' })  export class Log_documentoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Log_documento[] = [];     log_documentoAux : Log_documento;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.log_documentoAux = new Log_documento(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.log_documento.Log_documento';         dataAux.objetoJson = this.log_documentoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Log_documento. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


