import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Estado } from '../../../modelo/Estado'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'estado-selector',   templateUrl: 'estado.component.html' })  export class EstadoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Estado[] = [];     estadoAux : Estado;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.estadoAux = new Estado(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.estado.Estado';         dataAux.objetoJson = this.estadoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Estado. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


