import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Pic } from '../../../modelo/Pic'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'pic-selector',   templateUrl: 'pic.component.html' })  export class PicComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Pic[] = [];     picAux : Pic;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.picAux = new Pic(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.pic.Pic';         dataAux.objetoJson = this.picAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Pic. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


