import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Evidencia_resposta_exec } from '../../../modelo/Evidencia_resposta_exec'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'evidencia_resposta_exec-selector',   templateUrl: 'evidencia_resposta_exec.component.html' })  export class Evidencia_resposta_execComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Evidencia_resposta_exec[] = [];     evidencia_resposta_execAux : Evidencia_resposta_exec;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.evidencia_resposta_execAux = new Evidencia_resposta_exec(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.evidencia_resposta_exec.Evidencia_resposta_exec';         dataAux.objetoJson = this.evidencia_resposta_execAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Evidencia_resposta_exec. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


