import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Modulo } from '../../../modelo/Modulo'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'modulo-selector',   templateUrl: 'modulo.component.html' })  export class ModuloComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Modulo[] = [];     moduloAux : Modulo;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.moduloAux = new Modulo(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.modulo.Modulo';         dataAux.objetoJson = this.moduloAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Modulo. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


