import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Tipo_notificacao } from '../../../modelo/Tipo_notificacao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'tipo_notificacao-selector',   templateUrl: 'tipo_notificacao.component.html' })  export class Tipo_notificacaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Tipo_notificacao[] = [];     tipo_notificacaoAux : Tipo_notificacao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.tipo_notificacaoAux = new Tipo_notificacao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.tipo_notificacao.Tipo_notificacao';         dataAux.objetoJson = this.tipo_notificacaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Tipo_notificacao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


