import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Condicao } from '../../../modelo/Condicao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'condicao-selector',   templateUrl: 'condicao.component.html' })  export class CondicaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Condicao[] = [];     condicaoAux : Condicao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.condicaoAux = new Condicao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.condicao.Condicao';         dataAux.objetoJson = this.condicaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Condicao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


