import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Programa } from '../../../modelo/Programa'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'programa-selector',   templateUrl: 'programa.component.html' })  export class ProgramaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Programa[] = [];     programaAux : Programa;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.programaAux = new Programa(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.programa.Programa';         dataAux.objetoJson = this.programaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Programa. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


