import { Component } from '@angular/core';
import { ControleService } from './core/services/controle/controle.service';

@Component({ 
  moduleId: module.id,
  selector: 'app-container',
  template: `
    <nav class="navbar navbar-fluid">
        <div class="navbar-header">
        <a class="navbar-brand" href="#">
            <!--<img alt="Brand" src="../images/people.png">-->
        </a>
        </div>
        <div class="container-fluid navbar-right">
            <menu-topo></menu-topo>
        </div>
    </nav>
    <router-outlet></router-outlet>

    <!--<footer>
        <div class="navbar navbar-fixed-bottom">
            <div class="navbar-inner footer">
                <div class="container">
                    <footer>
                        <div class="row">
                            <div class="col-md-12">
                                <h4>Life Software Development</h4>
                            </div>
                        </div>
                    </footer>
                </div>
            </div>
        </div>
    </footer>-->`
})

export class AppComponent {

    constructor(private controleApp: ControleService) {
        // Try HTML5 geolocation.
        if (navigator.geolocation) {

            navigator.geolocation.getCurrentPosition(function(position : any) {
                console.log(position);
                console.log('position.coords.latitude: '  + Number(position.coords.latitude));
                console.log('position.coords.longitude: ' + position.coords.longitude);

                controleApp.latitudeUsuario     = position.coords.latitude;
                controleApp.longitudeUsuario    = position.coords.longitude;

                console.log('controleApp.latitudeUsuario: '  + controleApp.latitudeUsuario);
                console.log('controleApp.longitudeUsuario: ' + controleApp.longitudeUsuario);

            }, function() {
                //handleLocationError(true, infoWindow, map.getCenter());
                console.log('Handler error:');
            });
        } else {
            // Browser doesn't support Geolocation
            //handleLocationError(false, infoWindow, map.getCenter());
            console.log('Handler error 2');
        }
    }
}


