import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Tabela } from '../../../modelo/Tabela'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'tabela-selector',   templateUrl: 'tabela.component.html' })  export class TabelaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Tabela[] = [];     tabelaAux : Tabela;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.tabelaAux = new Tabela(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.tabela.Tabela';         dataAux.objetoJson = this.tabelaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Tabela. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


