import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Execucao_auditoria } from '../../../modelo/Execucao_auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'execucao_auditoria-selector',   templateUrl: 'execucao_auditoria.component.html' })  export class Execucao_auditoriaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Execucao_auditoria[] = [];     execucao_auditoriaAux : Execucao_auditoria;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.execucao_auditoriaAux = new Execucao_auditoria(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.execucao_auditoria.Execucao_auditoria';         dataAux.objetoJson = this.execucao_auditoriaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Execucao_auditoria. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


