import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Config_sistema } from '../../../modelo/Config_sistema'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'config_sistema-selector',   templateUrl: 'config_sistema.component.html' })  export class Config_sistemaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Config_sistema[] = [];     config_sistemaAux : Config_sistema;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.config_sistemaAux = new Config_sistema(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.config_sistema.Config_sistema';         dataAux.objetoJson = this.config_sistemaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Config_sistema. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


