import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Descricao_funcional } from '../../../modelo/Descricao_funcional'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'descricao_funcional-selector',   templateUrl: 'descricao_funcional.component.html' })  export class Descricao_funcionalComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Descricao_funcional[] = [];     descricao_funcionalAux : Descricao_funcional;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.descricao_funcionalAux = new Descricao_funcional(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.descricao_funcional.Descricao_funcional';         dataAux.objetoJson = this.descricao_funcionalAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Descricao_funcional. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


