import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Cronograma_manutencao } from '../../../modelo/Cronograma_manutencao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'cronograma_manutencao-selector',   templateUrl: 'cronograma_manutencao.component.html' })  export class Cronograma_manutencaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Cronograma_manutencao[] = [];     cronograma_manutencaoAux : Cronograma_manutencao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.cronograma_manutencaoAux = new Cronograma_manutencao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.cronograma_manutencao.Cronograma_manutencao';         dataAux.objetoJson = this.cronograma_manutencaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Cronograma_manutencao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


