import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Ocorrencia_periodicidade } from '../../../modelo/Ocorrencia_periodicidade'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'ocorrencia_periodicidade-selector',   templateUrl: 'ocorrencia_periodicidade.component.html' })  export class Ocorrencia_periodicidadeComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Ocorrencia_periodicidade[] = [];     ocorrencia_periodicidadeAux : Ocorrencia_periodicidade;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.ocorrencia_periodicidadeAux = new Ocorrencia_periodicidade(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.ocorrencia_periodicidade.Ocorrencia_periodicidade';         dataAux.objetoJson = this.ocorrencia_periodicidadeAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Ocorrencia_periodicidade. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


