import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Diagrama_helpdesk } from '../../../modelo/Diagrama_helpdesk'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'diagrama_helpdesk-selector',   templateUrl: 'diagrama_helpdesk.component.html' })  export class Diagrama_helpdeskComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Diagrama_helpdesk[] = [];     diagrama_helpdeskAux : Diagrama_helpdesk;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.diagrama_helpdeskAux = new Diagrama_helpdesk(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.diagrama_helpdesk.Diagrama_helpdesk';         dataAux.objetoJson = this.diagrama_helpdeskAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Diagrama_helpdesk. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


