import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Campo_agrupador } from '../../../modelo/Campo_agrupador'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'campo_agrupador-selector',   templateUrl: 'campo_agrupador.component.html' })  export class Campo_agrupadorComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Campo_agrupador[] = [];     campo_agrupadorAux : Campo_agrupador;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.campo_agrupadorAux = new Campo_agrupador(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.campo_agrupador.Campo_agrupador';         dataAux.objetoJson = this.campo_agrupadorAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Campo_agrupador. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


