import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Doc_descritivo_treinamento } from '../../../modelo/Doc_descritivo_treinamento'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'doc_descritivo_treinamento-selector',   templateUrl: 'doc_descritivo_treinamento.component.html' })  export class Doc_descritivo_treinamentoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Doc_descritivo_treinamento[] = [];     doc_descritivo_treinamentoAux : Doc_descritivo_treinamento;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.doc_descritivo_treinamentoAux = new Doc_descritivo_treinamento(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.doc_descritivo_treinamento.Doc_descritivo_treinamento';         dataAux.objetoJson = this.doc_descritivo_treinamentoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Doc_descritivo_treinamento. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


