import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Cronograma_auditoria } from '../../../modelo/Cronograma_auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'cronograma_auditoria-selector',   templateUrl: 'cronograma_auditoria.component.html' })  export class Cronograma_auditoriaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Cronograma_auditoria[] = [];     cronograma_auditoriaAux : Cronograma_auditoria;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.cronograma_auditoriaAux = new Cronograma_auditoria(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.cronograma_auditoria.Cronograma_auditoria';         dataAux.objetoJson = this.cronograma_auditoriaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Cronograma_auditoria. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


