import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Pessoa } from '../../../modelo/Pessoa'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'pessoa-selector',   templateUrl: 'pessoa.component.html' })  export class PessoaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Pessoa[] = [];     pessoaAux : Pessoa;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.pessoaAux = new Pessoa(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.pessoa.Pessoa';         dataAux.objetoJson = this.pessoaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Pessoa. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


