import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Manutencao_preventiva } from '../../../modelo/Manutencao_preventiva'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'manutencao_preventiva-selector',   templateUrl: 'manutencao_preventiva.component.html' })  export class Manutencao_preventivaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Manutencao_preventiva[] = [];     manutencao_preventivaAux : Manutencao_preventiva;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.manutencao_preventivaAux = new Manutencao_preventiva(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.manutencao_preventiva.Manutencao_preventiva';         dataAux.objetoJson = this.manutencao_preventivaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Manutencao_preventiva. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


