import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Evidencia_helpdesk } from '../../../modelo/Evidencia_helpdesk'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'evidencia_helpdesk-selector',   templateUrl: 'evidencia_helpdesk.component.html' })  export class Evidencia_helpdeskComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Evidencia_helpdesk[] = [];     evidencia_helpdeskAux : Evidencia_helpdesk;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.evidencia_helpdeskAux = new Evidencia_helpdesk(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.evidencia_helpdesk.Evidencia_helpdesk';         dataAux.objetoJson = this.evidencia_helpdeskAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Evidencia_helpdesk. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


