import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Acao_corretiva_formulario } from '../../../modelo/Acao_corretiva_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'acao_corretiva_formulario-selector',   templateUrl: 'acao_corretiva_formulario.component.html' })  export class Acao_corretiva_formularioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Acao_corretiva_formulario[] = [];     acao_corretiva_formularioAux : Acao_corretiva_formulario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.acao_corretiva_formularioAux = new Acao_corretiva_formulario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.acao_corretiva_formulario.Acao_corretiva_formulario';         dataAux.objetoJson = this.acao_corretiva_formularioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Acao_corretiva_formulario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


