import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Campo_indicador } from '../../../modelo/Campo_indicador'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'campo_indicador-selector',   templateUrl: 'campo_indicador.component.html' })  export class Campo_indicadorComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Campo_indicador[] = [];     campo_indicadorAux : Campo_indicador;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.campo_indicadorAux = new Campo_indicador(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.campo_indicador.Campo_indicador';         dataAux.objetoJson = this.campo_indicadorAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Campo_indicador. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


