import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Empresa } from '../../../modelo/Empresa'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'empresa-selector',   templateUrl: 'empresa.component.html' })  export class EmpresaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Empresa[] = [];     empresaAux : Empresa;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.empresaAux = new Empresa(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.empresa.Empresa';         dataAux.objetoJson = this.empresaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Empresa. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


