import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Tipo_turno } from '../../../modelo/Tipo_turno'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'tipo_turno-selector',   templateUrl: 'tipo_turno.component.html' })  export class Tipo_turnoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Tipo_turno[] = [];     tipo_turnoAux : Tipo_turno;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.tipo_turnoAux = new Tipo_turno(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.tipo_turno.Tipo_turno';         dataAux.objetoJson = this.tipo_turnoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Tipo_turno. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


