import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Anexo } from '../../../modelo/Anexo'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'anexo-selector',   templateUrl: 'anexo.component.html' })  export class AnexoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Anexo[] = [];     anexoAux : Anexo;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.anexoAux = new Anexo(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.anexo.Anexo';         dataAux.objetoJson = this.anexoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Anexo. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


