import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Exame_funcao } from '../../../modelo/Exame_funcao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'exame_funcao-selector',   templateUrl: 'exame_funcao.component.html' })  export class Exame_funcaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Exame_funcao[] = [];     exame_funcaoAux : Exame_funcao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.exame_funcaoAux = new Exame_funcao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.exame_funcao.Exame_funcao';         dataAux.objetoJson = this.exame_funcaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Exame_funcao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


