import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Config_campo_pesquisa } from '../../../modelo/Config_campo_pesquisa'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'config_campo_pesquisa-selector',   templateUrl: 'config_campo_pesquisa.component.html' })  export class Config_campo_pesquisaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Config_campo_pesquisa[] = [];     config_campo_pesquisaAux : Config_campo_pesquisa;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.config_campo_pesquisaAux = new Config_campo_pesquisa(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.config_campo_pesquisa.Config_campo_pesquisa';         dataAux.objetoJson = this.config_campo_pesquisaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Config_campo_pesquisa. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


