import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Resposta_exec_formulario } from '../../../modelo/Resposta_exec_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'resposta_exec_formulario-selector',   templateUrl: 'resposta_exec_formulario.component.html' })  export class Resposta_exec_formularioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Resposta_exec_formulario[] = [];     resposta_exec_formularioAux : Resposta_exec_formulario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.resposta_exec_formularioAux = new Resposta_exec_formulario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.resposta_exec_formulario.Resposta_exec_formulario';         dataAux.objetoJson = this.resposta_exec_formularioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Resposta_exec_formulario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


