import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Permissao_acesso_documento } from '../../../modelo/Permissao_acesso_documento'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'permissao_acesso_documento-selector',   templateUrl: 'permissao_acesso_documento.component.html' })  export class Permissao_acesso_documentoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Permissao_acesso_documento[] = [];     permissao_acesso_documentoAux : Permissao_acesso_documento;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.permissao_acesso_documentoAux = new Permissao_acesso_documento(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.permissao_acesso_documento.Permissao_acesso_documento';         dataAux.objetoJson = this.permissao_acesso_documentoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Permissao_acesso_documento. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


