import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Fluxograma } from '../../../modelo/Fluxograma'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'fluxograma-selector',   templateUrl: 'fluxograma.component.html' })  export class FluxogramaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Fluxograma[] = [];     fluxogramaAux : Fluxograma;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.fluxogramaAux = new Fluxograma(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.fluxograma.Fluxograma';         dataAux.objetoJson = this.fluxogramaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Fluxograma. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


