import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Conclusao_auditoria } from '../../../modelo/Conclusao_auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'conclusao_auditoria-selector',   templateUrl: 'conclusao_auditoria.component.html' })  export class Conclusao_auditoriaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Conclusao_auditoria[] = [];     conclusao_auditoriaAux : Conclusao_auditoria;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.conclusao_auditoriaAux = new Conclusao_auditoria(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.conclusao_auditoria.Conclusao_auditoria';         dataAux.objetoJson = this.conclusao_auditoriaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Conclusao_auditoria. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


