import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Acao_preventiva_pa } from '../../../modelo/Acao_preventiva_pa'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'acao_preventiva_pa-selector',   templateUrl: 'acao_preventiva_pa.component.html' })  export class Acao_preventiva_paComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Acao_preventiva_pa[] = [];     acao_preventiva_paAux : Acao_preventiva_pa;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.acao_preventiva_paAux = new Acao_preventiva_pa(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.acao_preventiva_pa.Acao_preventiva_pa';         dataAux.objetoJson = this.acao_preventiva_paAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Acao_preventiva_pa. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


