import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Cliente } from '../../../modelo/Cliente'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'cliente-selector',   templateUrl: 'cliente.component.html' })  export class ClienteComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Cliente[] = [];     clienteAux : Cliente;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.clienteAux = new Cliente(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.cliente.Cliente';         dataAux.objetoJson = this.clienteAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Cliente. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


