import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Tipo_campo } from '../../../modelo/Tipo_campo'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'tipo_campo-selector',   templateUrl: 'tipo_campo.component.html' })  export class Tipo_campoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Tipo_campo[] = [];     tipo_campoAux : Tipo_campo;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.tipo_campoAux = new Tipo_campo(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.tipo_campo.Tipo_campo';         dataAux.objetoJson = this.tipo_campoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Tipo_campo. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


