import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Permissao_menu_indicadores } from '../../../modelo/Permissao_menu_indicadores'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'permissao_menu_indicadores-selector',   templateUrl: 'permissao_menu_indicadores.component.html' })  export class Permissao_menu_indicadoresComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Permissao_menu_indicadores[] = [];     permissao_menu_indicadoresAux : Permissao_menu_indicadores;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.permissao_menu_indicadoresAux = new Permissao_menu_indicadores(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.permissao_menu_indicadores.Permissao_menu_indicadores';         dataAux.objetoJson = this.permissao_menu_indicadoresAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Permissao_menu_indicadores. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


