import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Plano_acao_formulario } from '../../../modelo/Plano_acao_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'plano_acao_formulario-selector',   templateUrl: 'plano_acao_formulario.component.html' })  export class Plano_acao_formularioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Plano_acao_formulario[] = [];     plano_acao_formularioAux : Plano_acao_formulario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.plano_acao_formularioAux = new Plano_acao_formulario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.plano_acao_formulario.Plano_acao_formulario';         dataAux.objetoJson = this.plano_acao_formularioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Plano_acao_formulario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


