import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Acao_corretiva_helpdesk } from '../../../modelo/Acao_corretiva_helpdesk'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'acao_corretiva_helpdesk-selector',   templateUrl: 'acao_corretiva_helpdesk.component.html' })  export class Acao_corretiva_helpdeskComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Acao_corretiva_helpdesk[] = [];     acao_corretiva_helpdeskAux : Acao_corretiva_helpdesk;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.acao_corretiva_helpdeskAux = new Acao_corretiva_helpdesk(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.acao_corretiva_helpdesk.Acao_corretiva_helpdesk';         dataAux.objetoJson = this.acao_corretiva_helpdeskAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Acao_corretiva_helpdesk. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


