import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Grupo_formulario } from '../../../modelo/Grupo_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'grupo_formulario-selector',   templateUrl: 'grupo_formulario.component.html' })  export class Grupo_formularioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Grupo_formulario[] = [];     grupo_formularioAux : Grupo_formulario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.grupo_formularioAux = new Grupo_formulario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.grupo_formulario.Grupo_formulario';         dataAux.objetoJson = this.grupo_formularioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Grupo_formulario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


