import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Permissao_acesso } from '../../../modelo/Permissao_acesso'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'permissao_acesso-selector',   templateUrl: 'permissao_acesso.component.html' })  export class Permissao_acessoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Permissao_acesso[] = [];     permissao_acessoAux : Permissao_acesso;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.permissao_acessoAux = new Permissao_acesso(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.permissao_acesso.Permissao_acesso';         dataAux.objetoJson = this.permissao_acessoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Permissao_acesso. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


