import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Categoria_ishikawa } from '../../../modelo/Categoria_ishikawa'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'categoria_ishikawa-selector',   templateUrl: 'categoria_ishikawa.component.html' })  export class Categoria_ishikawaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Categoria_ishikawa[] = [];     categoria_ishikawaAux : Categoria_ishikawa;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.categoria_ishikawaAux = new Categoria_ishikawa(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.categoria_ishikawa.Categoria_ishikawa';         dataAux.objetoJson = this.categoria_ishikawaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Categoria_ishikawa. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


