import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Config_pesquisa } from '../../../modelo/Config_pesquisa'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'config_pesquisa-selector',   templateUrl: 'config_pesquisa.component.html' })  export class Config_pesquisaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Config_pesquisa[] = [];     config_pesquisaAux : Config_pesquisa;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.config_pesquisaAux = new Config_pesquisa(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.config_pesquisa.Config_pesquisa';         dataAux.objetoJson = this.config_pesquisaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Config_pesquisa. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


