import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Checagem_documento } from '../../../modelo/Checagem_documento'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'checagem_documento-selector',   templateUrl: 'checagem_documento.component.html' })  export class Checagem_documentoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Checagem_documento[] = [];     checagem_documentoAux : Checagem_documento;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.checagem_documentoAux = new Checagem_documento(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.checagem_documento.Checagem_documento';         dataAux.objetoJson = this.checagem_documentoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Checagem_documento. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


