import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Descritivo_treinamento } from '../../../modelo/Descritivo_treinamento'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'descritivo_treinamento-selector',   templateUrl: 'descritivo_treinamento.component.html' })  export class Descritivo_treinamentoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Descritivo_treinamento[] = [];     descritivo_treinamentoAux : Descritivo_treinamento;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.descritivo_treinamentoAux = new Descritivo_treinamento(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.descritivo_treinamento.Descritivo_treinamento';         dataAux.objetoJson = this.descritivo_treinamentoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Descritivo_treinamento. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


