import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Ponto_mapa } from '../../../modelo/Ponto_mapa'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'ponto_mapa-selector',   templateUrl: 'ponto_mapa.component.html' })  export class Ponto_mapaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Ponto_mapa[] = [];     ponto_mapaAux : Ponto_mapa;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.ponto_mapaAux = new Ponto_mapa(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.ponto_mapa.Ponto_mapa';         dataAux.objetoJson = this.ponto_mapaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Ponto_mapa. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


