import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Configuracao_ouvidoria } from '../../../modelo/Configuracao_ouvidoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'configuracao_ouvidoria-selector',   templateUrl: 'configuracao_ouvidoria.component.html' })  export class Configuracao_ouvidoriaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Configuracao_ouvidoria[] = [];     configuracao_ouvidoriaAux : Configuracao_ouvidoria;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.configuracao_ouvidoriaAux = new Configuracao_ouvidoria(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.configuracao_ouvidoria.Configuracao_ouvidoria';         dataAux.objetoJson = this.configuracao_ouvidoriaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Configuracao_ouvidoria. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


