import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Notificacao_grupo_usuario } from '../../../modelo/Notificacao_grupo_usuario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'notificacao_grupo_usuario-selector',   templateUrl: 'notificacao_grupo_usuario.component.html' })  export class Notificacao_grupo_usuarioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Notificacao_grupo_usuario[] = [];     notificacao_grupo_usuarioAux : Notificacao_grupo_usuario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.notificacao_grupo_usuarioAux = new Notificacao_grupo_usuario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.notificacao_grupo_usuario.Notificacao_grupo_usuario';         dataAux.objetoJson = this.notificacao_grupo_usuarioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Notificacao_grupo_usuario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


