import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Campo } from '../../../modelo/Campo'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'campo-selector',   templateUrl: 'campo.component.html' })  export class CampoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Campo[] = [];     campoAux : Campo;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.campoAux = new Campo(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.campo.Campo';         dataAux.objetoJson = this.campoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Campo. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


