import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Participantes_reuniao } from '../../../modelo/Participantes_reuniao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'participantes_reuniao-selector',   templateUrl: 'participantes_reuniao.component.html' })  export class Participantes_reuniaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Participantes_reuniao[] = [];     participantes_reuniaoAux : Participantes_reuniao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.participantes_reuniaoAux = new Participantes_reuniao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.participantes_reuniao.Participantes_reuniao';         dataAux.objetoJson = this.participantes_reuniaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Participantes_reuniao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


