import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Pre_avaliacao_ideia_pic } from '../../../modelo/Pre_avaliacao_ideia_pic'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'pre_avaliacao_ideia_pic-selector',   templateUrl: 'pre_avaliacao_ideia_pic.component.html' })  export class Pre_avaliacao_ideia_picComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Pre_avaliacao_ideia_pic[] = [];     pre_avaliacao_ideia_picAux : Pre_avaliacao_ideia_pic;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.pre_avaliacao_ideia_picAux = new Pre_avaliacao_ideia_pic(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.pre_avaliacao_ideia_pic.Pre_avaliacao_ideia_pic';         dataAux.objetoJson = this.pre_avaliacao_ideia_picAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Pre_avaliacao_ideia_pic. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


