import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Helpdesk_ocorrencia } from '../../../modelo/Helpdesk_ocorrencia'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'helpdesk_ocorrencia-selector',   templateUrl: 'helpdesk_ocorrencia.component.html' })  export class Helpdesk_ocorrenciaComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Helpdesk_ocorrencia[] = [];     helpdesk_ocorrenciaAux : Helpdesk_ocorrencia;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.helpdesk_ocorrenciaAux = new Helpdesk_ocorrencia(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.helpdesk_ocorrencia.Helpdesk_ocorrencia';         dataAux.objetoJson = this.helpdesk_ocorrenciaAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Helpdesk_ocorrencia. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


