import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Plano_acao } from '../../../modelo/Plano_acao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'plano_acao-selector',   templateUrl: 'plano_acao.component.html' })  export class Plano_acaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Plano_acao[] = [];     plano_acaoAux : Plano_acao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.plano_acaoAux = new Plano_acao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.plano_acao.Plano_acao';         dataAux.objetoJson = this.plano_acaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Plano_acao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


