import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Equipamento } from '../../../modelo/Equipamento'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'equipamento-selector',   templateUrl: 'equipamento.component.html' })  export class EquipamentoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Equipamento[] = [];     equipamentoAux : Equipamento;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.equipamentoAux = new Equipamento(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.equipamento.Equipamento';         dataAux.objetoJson = this.equipamentoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Equipamento. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


