import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Causa_efeito } from '../../../modelo/Causa_efeito'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'causa_efeito-selector',   templateUrl: 'causa_efeito.component.html' })  export class Causa_efeitoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Causa_efeito[] = [];     causa_efeitoAux : Causa_efeito;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.causa_efeitoAux = new Causa_efeito(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.causa_efeito.Causa_efeito';         dataAux.objetoJson = this.causa_efeitoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Causa_efeito. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


