import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Acao_preventiva_formulario } from '../../../modelo/Acao_preventiva_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'acao_preventiva_formulario-selector',   templateUrl: 'acao_preventiva_formulario.component.html' })  export class Acao_preventiva_formularioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Acao_preventiva_formulario[] = [];     acao_preventiva_formularioAux : Acao_preventiva_formulario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.acao_preventiva_formularioAux = new Acao_preventiva_formulario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.acao_preventiva_formulario.Acao_preventiva_formulario';         dataAux.objetoJson = this.acao_preventiva_formularioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Acao_preventiva_formulario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


