import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Exame_medico } from '../../../modelo/Exame_medico'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'exame_medico-selector',   templateUrl: 'exame_medico.component.html' })  export class Exame_medicoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Exame_medico[] = [];     exame_medicoAux : Exame_medico;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.exame_medicoAux = new Exame_medico(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.exame_medico.Exame_medico';         dataAux.objetoJson = this.exame_medicoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Exame_medico. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


