import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Formulario_indicador } from '../../../modelo/Formulario_indicador'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'formulario_indicador-selector',   templateUrl: 'formulario_indicador.component.html' })  export class Formulario_indicadorComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Formulario_indicador[] = [];     formulario_indicadorAux : Formulario_indicador;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.formulario_indicadorAux = new Formulario_indicador(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.formulario_indicador.Formulario_indicador';         dataAux.objetoJson = this.formulario_indicadorAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Formulario_indicador. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


