import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Setor } from '../../../modelo/Setor'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'setor-selector',   templateUrl: 'setor.component.html' })  export class SetorComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Setor[] = [];     setorAux : Setor;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.setorAux = new Setor(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.setor.Setor';         dataAux.objetoJson = this.setorAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Setor. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


