import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Funcao_usuario } from '../../../modelo/Funcao_usuario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'funcao_usuario-selector',   templateUrl: 'funcao_usuario.component.html' })  export class Funcao_usuarioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Funcao_usuario[] = [];     funcao_usuarioAux : Funcao_usuario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.funcao_usuarioAux = new Funcao_usuario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.funcao_usuario.Funcao_usuario';         dataAux.objetoJson = this.funcao_usuarioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Funcao_usuario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


