import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Permissao_indicador } from '../../../modelo/Permissao_indicador'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'permissao_indicador-selector',   templateUrl: 'permissao_indicador.component.html' })  export class Permissao_indicadorComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Permissao_indicador[] = [];     permissao_indicadorAux : Permissao_indicador;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.permissao_indicadorAux = new Permissao_indicador(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.permissao_indicador.Permissao_indicador';         dataAux.objetoJson = this.permissao_indicadorAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Permissao_indicador. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


