import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Escalonamento_formulario } from '../../../modelo/Escalonamento_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'escalonamento_formulario-selector',   templateUrl: 'escalonamento_formulario.component.html' })  export class Escalonamento_formularioComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Escalonamento_formulario[] = [];     escalonamento_formularioAux : Escalonamento_formulario;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.escalonamento_formularioAux = new Escalonamento_formulario(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.escalonamento_formulario.Escalonamento_formulario';         dataAux.objetoJson = this.escalonamento_formularioAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Escalonamento_formulario. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


