import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Treinamento_funcao } from '../../../modelo/Treinamento_funcao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'treinamento_funcao-selector',   templateUrl: 'treinamento_funcao.component.html' })  export class Treinamento_funcaoComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Treinamento_funcao[] = [];     treinamento_funcaoAux : Treinamento_funcao;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.treinamento_funcaoAux = new Treinamento_funcao(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.treinamento_funcao.Treinamento_funcao';         dataAux.objetoJson = this.treinamento_funcaoAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Treinamento_funcao. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


