import { Component, Input, OnInit } from '@angular/core'; import { Router } from '@angular/router'; import { Modulo_indicadores } from '../../../modelo/Modulo_indicadores'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual';  import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'modulo_indicadores-selector',   templateUrl: 'modulo_indicadores.component.html' })  export class Modulo_indicadoresComponent implements OnInit { 	 	errorMessage : string = '';      @Input() listaDados : Modulo_indicadores[] = [];     modulo_indicadoresAux : Modulo_indicadores;     constructor(        private dataService: PesquisaService,        private router: Router) { }      ngOnInit() {         this.modulo_indicadoresAux = new Modulo_indicadores(); let dataAux : ObjetoVirtual = new ObjetoVirtual();         dataAux.classeJava  = 'br.com.primum.modelo.modulo_indicadores.Modulo_indicadores';         dataAux.objetoJson = this.modulo_indicadoresAux;          this.dataService.efetuarPesquisa(dataAux)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);     }  	onEfetuarPesquisa(result : any) { 		if (!result) { 			this.errorMessage = 'Houve um erro a pesquisa de Modulo_indicadores. ';             this.router.navigate(['/home']); 		}          this.listaDados = result; 	} }


